<template>
  <img alt="Vue logo" src="./assets/adminname.png">
  <h1>{{ firstname }} {{ lastname }}</h1>
  <!-- v-on:click หรือ @click -->
  <button v-on:click="showalertFirstname">แสดงชื่อ</button>
  <button @click="showalertLastname">แสดงนามสกุล</button>
  <h1>{{ Age }}</h1>
  <button @click="plus">เพิ่มตัวเลข</button>
  <button @click="minus">ลบตัวเลข</button>
  <button @click.right="plus5(5)">กดคลิกขวา เพิ่ม 5 ตัวเลข</button>
  <button @click="minus5(5)">ลบ 5 ตัวเลข</button>
  <p>=-=-=-=-=-=-=-=-=-=-=-=-=-=-=-=-=-=-=-=-=-=-=-=-=-=-=-=</p>
  <!-- v-on:input หรือ @input -->
  <form action=""><label for="">กรุณากรอบชื่อเล่น : </label><input type="text" @input="setnickname"></form>
  <form action=""><label for="">กรุณากรอบชื่อจริง : </label><input type="text" @input="setfirstname"></form>
  <form action=""><label for="">กรุณากรอบชื่อนามสกุล : </label><input type="text" @input="setlastname"></form>
  <form action=""><label for="">กรุณากรอบอายุ : </label><input type="text" @input="setage"></form>
  <form action=""><label for="">กรุณากรอบวัน/เดือน/ปีเกิด : </label><input type="text" @input="setdate"></form>
  <form action=""><label for="">กรุณากรอบสาขา : </label><input type="text" @input="setbranch"></form>
  <form action=""><label for="">กรุณากรอบห้อง : </label><input type="text" @input="setroom"></form>
  <form action=""><label for="">กรุณากรอบคดิประจำใจ : </label><input type="text" @input="setmotto"></form>
  <form action=""><label for="">กรุณากรอบสถานที่เรียนต่อ : </label><input type="text" @input="setstudyPlace"></form>
  <form action=""><label for="">กรุณากรอบวิชาที่ไม่ชอบ : </label><input type="text" @input="setsubjectDonotLike"></form>
  <p>=-=-=-=-=-=-=-=-=-=-=-=-=-=-=-=-=-=-=-=-=-=-=-=-=-=-=-=</p>
  <h1>แสดงชื่อเล่น : {{nickname}} </h1>
  <h1>แสดงชื่อจริง : {{firstname}} </h1>
  <h1>แสดงชื่อนามสกุล : {{lastname}} </h1>
  <h1>แสดงอายุ : {{age}} </h1>
  <h1>แสดงวัน/เดือน/ปีเกิด : {{date}} </h1>
  <h1>แสดงสาขา : {{branch}} </h1>
  <h1>แสดงห้อง : {{room}} </h1>
  <h1>แสดงคดิประจำใจ : {{motto}} </h1>
  <h1>แสดงสถานที่เรียนต่อ : {{studyPlace}} </h1>
  <h1>แสดงวิชาที่ไม่ชอบ : {{subjectDonotLike}} </h1>
</template>

<script>

export default {
  name: 'App',
  data(){
    return{
      Firstname: 'Kanitsorn',
      Lastname: 'Songthwee',
      Age: '19',
      nickname: '',
      firstname: '',
      lastname: '',
      age: '',
      date: '',
      branch: '',
      room: '',
      motto: '',
      studyPlace: '',
      subjectDonotLike: ''
    }
  },
  methods: {
    showalertFirstname(){alert(this.Firstname)},
    showalertLastname(){alert(this.Lastname)},
    plus(){this.Age++},
    minus(){this.Age--},
    plus5(val){this.Age+=val},
    minus5(val){this.Age-=val},
    // แสดงที่ Console
    // setnickname(event){console.log(event.target.value)}
    setnickname(event){this.nickname=event.target.value},
    setfirstname(event){this.firstname=event.target.value},
    setlastname(event){this.lastname=event.target.value},
    setage(event){this.age=event.target.value},
    setdate(event){this.date=event.target.value},
    setbranch(event){this.branch=event.target.value},
    setroom(event){this.room=event.target.value},
    setmotto(event){this.motto=event.target.value},
    setstudyPlace(event){this.studyPlace=event.target.value},
    setsubjectDonotLike(event){this.subjectDonotLike=event.target.value}
  }
}
</script>

